import React, { useState } from "react";
import { Menu, X } from "lucide-react"; // You can use Heroicons or Lucide

export default function Navbar() {
  const [isOpen, setIsOpen] = useState(false);

  const userType = "admin";
  return (
    <>
      {/* Mobile Header */}
      <div className="md:hidden bg-black text-white flex justify-between items-center px-4 py-3">
        <div className="flex items-start gap-2">
          <img
            src="/icons/person-1.svg"
            alt="Dispute Resolutions"
            className="w-10 h-10"
          />
          <div>
            <h1 className="text-lg font-bold text-blue-500">DISPUTE</h1>
            <span className="text-xs text-gray-400">RESOLUTIONS</span>
          </div>
        </div>
        <button onClick={() => setIsOpen(!isOpen)}>
          {isOpen ? <X size={28} /> : <Menu size={28} />}
        </button>
      </div>

      {/* Sidebar */}
      <div
        className={`fixed md:static top-0 left-0 h-[600px] w-60  bg-black text-white flex flex-col py-6 transform transition-transform duration-300 z-50
          ${isOpen ? "translate-x-0" : "-translate-x-full md:translate-x-0"}`}
      >
        {/* Logo */}
        <div className="flex flex-col items-center mb-10">
          <img
            src="/icons/person-1.svg"
            alt="Dispute Resolutions"
            className="w-20 h-20 object-contain"
          />
          <h1 className="text-lg font-bold tracking-wide text-blue-500">
            DISPUTE
          </h1>
          <span className="text-xs tracking-widest text-gray-400">
            RESOLUTIONS
          </span>
        </div>

        {/* Links */}
        {userType === "member" ? (
          <div className="flex flex-col text-left gap-5 text-sm w-full px-6">
            <span className="text-orange-500">Hello</span>
            <a href="/" className="font-bold hover:text-orange-400">
              Member Dashboard
            </a>
            <a href="/" className="hover:text-orange-400">
              Job Schedule
            </a>
            <a href="/myCurrentJobs" className="hover:text-orange-400">
              My Current Jobs
            </a>
            <a href="/myCompletedJobs" className="hover:text-orange-400">
              My Completed Jobs
            </a>
            <a href="/createJobs" className="hover:text-orange-400">
              Create Jobs
            </a>
            <a
              href="/myAccount"
              className="text-orange-500 hover:text-orange-400"
            >
              My Account
            </a>
          </div>
        ) : (
          <div className="flex flex-col text-left gap-5 text-sm w-full px-6">
            <span className="text-orange-500">Hello</span>
            <a href="/admin/jobList" className="font-bold hover:text-orange-400">
              Admin Dashboard
            </a>
            <a href="/admin/jobList" className="hover:text-orange-400">
              Job List
            </a>
            <a href="/admin/createJob" className="hover:text-orange-400">
              Create Jobs
            </a>
            <a href="/admin/memberApprove" className="hover:text-orange-400">
             Member Approve
            </a>
            <a
              href="/admin/account"
              className="text-orange-500 hover:text-orange-400"
            >
              My Account
            </a>
          </div>
        )}

        {/* Log Out */}
        <div className="mt-auto text-left px-6">
          <a href="#" className="hover:text-orange-400">
            Log Out
          </a>
        </div>
      </div>

      {/* Overlay for mobile */}
      {isOpen && (
        <div
          onClick={() => setIsOpen(false)}
          className="fixed inset-0 bg-black bg-opacity-50 z-40 md:hidden"
        />
      )}
    </>
  );
}
