import React, { useState } from "react";
import currentjobs from "../Data/currentJobs.json";
import Navbar from "../components/navbar";
import { FiFilter, FiPlus } from "react-icons/fi";
import Pagination from "@mui/material/Pagination";
import Stack from "@mui/material/Stack";

const PRODUCTS_PER_PAGE = 1;

export default function JobSchedule() {
  const [searchTerm, setSearchTerm] = useState("");
  const [currentPage, setCurrentPage] = useState(1);

  const filteredJobs = currentjobs.filter((job) =>
    (job.overview || "").toLowerCase().includes(searchTerm.toLowerCase())
  );

  const totalPages = Math.ceil(filteredJobs.length / PRODUCTS_PER_PAGE);

  const paginatedFilteredJobs = filteredJobs.slice(
    (currentPage - 1) * PRODUCTS_PER_PAGE,
    currentPage * PRODUCTS_PER_PAGE
  );

  const handlePageChange = (event, value) => {
    setCurrentPage(value);
  };

  return (
    <div className="flex flex-col md:flex-row min-h-screen">
      <Navbar />
      <div className="p-4 w-full overflow-x-hidden">
        <h4 className="font-semibold mb-4 text-lg md:text-xl">
          My Current Jobs
        </h4>

        {/* Top controls */}
        <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-3 mb-5 w-full">
          <div className="text-lg md:text-xl font-bold text-orange-500">
            Total Jobs {currentjobs.length}
          </div>
          <div className="flex flex-col sm:flex-row flex-wrap items-stretch sm:items-center gap-2 w-full md:w-auto">
            {/* Search */}
            <input
              type="text"
              placeholder="Search..."
              className="border border-gray-300 rounded-md px-3 py-2 text-sm w-full sm:w-auto focus:outline-none focus:ring-2 focus:ring-orange-500"
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
            />

            {/* Filter */}
            <button className="flex items-center justify-center gap-1 border border-gray-300 rounded-md px-3 py-2 text-sm hover:bg-gray-100 w-full sm:w-auto">
              <FiFilter /> Filter
            </button>

            {/* Add User */}
            <button className="flex items-center justify-center gap-1 bg-orange-500 text-white rounded-md px-3 py-2 text-sm hover:bg-orange-600 w-full sm:w-auto">
              <FiPlus /> Add User
            </button>
          </div>
        </div>

        {/* Table - scrollable for smaller screens */}
        <div className="overflow-x-auto">
          <table className="table-auto min-w-[650px] w-full text-sm">
            <thead>
              <tr className="bg-gray-100 border-b text-left text-[12px] border-b-gray-300">
                <th className="px-4 py-2">Job ID</th>
                <th className="px-4 py-2">Brief Overview</th>
                <th className="px-4 py-2">Venue</th>
                <th className="px-4 py-2">Duration</th>
                <th className="px-4 py-2">Remuneration</th>
                <th className="px-4 py-2">Action</th>
              </tr>
            </thead>
            <tbody>
              {paginatedFilteredJobs.map((job) => (
                <tr
                  key={job.id}
                  className="border-b text-left border-b-gray-300 text-[12px]"
                >
                  <td className="px-4 py-2">{job.id}</td>
                  <td className="px-4 py-2">
                    <div className="flex flex-col gap-1">
                      <div className="flex flex-wrap gap-2 items-center">
                        <h1>{job.overview}</h1>
                        <p className="bg-[#E0EEE0] hover:bg-green-400 text-center text-green-900 text-[8px] font-semibold px-2 rounded-3xl transition duration-200">
                          {job.status}
                        </p>
                      </div>
                      <p className="text-[10px]">{job.date}</p>
                    </div>
                  </td>
                  <td className="px-4 py-2">{job.venue}</td>
                  <td className="px-4 py-2">
                    <div className="flex flex-wrap gap-2">
                      <h1>{job.duration}</h1>
                      <p className="bg-[#DEEFFC] hover:bg-blue-400 text-center text-blue-700 text-[8px] px-2 font-semibold rounded-3xl transition duration-200">
                        {job.field}
                      </p>
                    </div>
                  </td>
                  <td className="px-4 py-2">{job.remuneration}</td>
                  <td className="px-4 py-2">
                    <div className="flex flex-col sm:flex-row gap-2">
                      <button className="bg-orange-500 hover:bg-orange-600 text-white text-[12px] font-semibold px-4 py-2 rounded-md transition duration-200 w-full sm:w-auto">
                        Completed
                      </button>
                      <button className="bg-orange-500 hover:bg-orange-600 text-white text-[12px] font-semibold px-4 py-2 rounded-md transition duration-200 w-full sm:w-auto">
                        Aborted
                      </button>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>

        {/* Pagination */}
        <div className="flex justify-center md:justify-end mt-5">
          <Stack spacing={2}>
            <Pagination
              count={totalPages}
              page={currentPage}
              onChange={handlePageChange}
              variant="outlined"
              className="flex justify-center"
              sx={{
                "& .MuiPaginationItem-root.Mui-selected": {
                  backgroundColor: "#f97316",
                  color: "white",
                  borderColor: "orange",
                },
                "& .MuiPaginationItem-root.Mui-selected:hover": {
                  backgroundColor: "#ea580c",
                  borderColor: "#ff8c00",
                },
              }}
            />
          </Stack>
        </div>
      </div>
    </div>
  );
}
